@import "assets/fonts/Merriweather/merriweather-font.css";

@import "assets/fonts/Montserrat/montserrat-font.css";

body {
  background: #EDECE9 url(./assets/images/Sun.svg) no-repeat fixed center center;
  background-size: contain;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Merriweather-Light, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.swiper-button-next:after {
  display: none;
}

.swiper-button-prev {
  &:after {
    display: none;
  }

  left: 0;
  cursor: url("./assets/images/sections/carCeram/arrow left.svg"), pointer;
}

.swiper-button-next {
  right: 0;
  cursor: url("./assets/images/sections/carCeram/arrow right.svg"), pointer;
}

.sec1Swiper .swiper-wrapper {
  margin-left: 150px;
}

div {
  &.ScrollbarsCustom-Track {
    width: 8px !important;
  }

  &.ScrollbarsCustom-Thumb {
    width: 6px !important;
  }

  &.ScrollbarsCustom {
    position: initial !important;
  }

  &.ScrollbarsCustom-Track {
    background: transparent !important;
    top: 3px !important;
    height: calc(100% - 6px) !important;
    z-index: 50;
  }

  &.ScrollbarsCustom-Thumb {
    background: #596460 !important;
  }

  &.ScrollbarsCustom-TrackX {
    display: none !important;
  }
}

@media (min-width: 320px) {
  .sec4Swiper .swiper-wrapper, .sec3Swiper .swiper-wrapper, .sec1Swiper .swiper-wrapper {
    margin-left: 10px;
  }

  .sec3Swiper .swiper-wrapper {
    height: 260px;
  }

  body {
    background-size: 200px 200px;
  }
}

@media (min-width: 400px) {
  .sec3Swiper .swiper-wrapper {
    height: 280px;
  }
}

@media (min-width: 420px) {
  .sec3Swiper .swiper-wrapper {
    height: 220px;
  }

  body {
    background-size: 250px 250px;
  }
}

@media (min-width: 500px) {
  .sec3Swiper .swiper-wrapper {
    height: 270px;
  }
}

@media (min-width: 600px) {
  .sec3Swiper .swiper-wrapper {
    height: 310px;
  }
}

@media (min-width: 650px) {
  .sec3Swiper .swiper-wrapper {
    height: 350px;
  }
}

@media (min-width: 768px) {
  .sec4Swiper .swiper-wrapper, .sec3Swiper .swiper-wrapper, .sec1Swiper .swiper-wrapper {
    margin-left: 20px;
  }

  .sec3Swiper .swiper-wrapper {
    height: 437px;
  }

  body {
    background-size: 400px 400px;
  }
}

@media (min-width: 950px) {
  .sec3Swiper {
    .swiper-button-prev, .swiper-button-next {
      display: none;
    }
  }
}

@media (min-width: 1024px) {
  .sec3Swiper {
    .swiper-wrapper {
      margin-left: 0;
      grid-area: content;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      grid-column-gap: 30px;
      height: 437px;

      .swiper-slide {
        margin-right: 0 !important;
        width: 100% !important;
      }
    }

    .swiper-container {
      display: grid;
      grid-template-columns: 20px 1fr 20px;
      grid-template-areas: "left-zero content right-zero";
    }
  }

  body {
    background-size: 450px 450px;
  }
}

@media (min-width: 1130px) {
  .sec3Swiper .swiper-wrapper {
    grid-template-columns: 1.5fr 1fr 1fr;
  }
}

@media (min-width: 1280px) {
  .sec4Swiper .swiper-wrapper, .sec1Swiper .swiper-wrapper {
    margin-left: 50px;
  }

  .sec3Swiper {
    .swiper-container {
      grid-template-columns: 50px 1fr 50px;
    }

    .swiper-wrapper {
      grid-template-columns: 1.556fr 1fr 1fr;
    }
  }

  body {
    background-size: 500px 500px;
  }
}

@media (min-width: 1450px) {
  .sec3Swiper .swiper-wrapper {
    height: 500px;
  }
}

@media (min-width: 1600px) {
  .sec4Swiper .swiper-wrapper, .sec1Swiper .swiper-wrapper {
    margin-left: 100px;
  }

  .sec3Swiper {
    .swiper-container {
      grid-template-columns: 100px 1fr 100px;
    }

    .swiper-wrapper {
      height: 600px;
    }
  }

  body {
    background-size: 625px 625px;
  }
}

@media (min-width: 1920px) {
  .sec4Swiper .swiper-wrapper, .sec1Swiper .swiper-wrapper {
    margin-left: 150px;
  }

  .sec3Swiper {
    .swiper-container {
      grid-template-columns: 150px 1fr 150px;
    }

    .swiper-wrapper {
      grid-template-columns: 1.75fr 1fr 1fr;
    }
  }

  body {
    background-size: 800px 800px;
  }
}

@media (min-width: 2300px) {
  .sec3Swiper .swiper-wrapper {
    height: 700px;
  }
}

@media (min-width: 2560px) {
  .sec4Swiper .swiper-wrapper, .sec1Swiper .swiper-wrapper {
    margin-left: 300px;
  }

  .sec3Swiper {
    .swiper-wrapper {
      height: 720px;
    }

    .swiper-container {
      grid-template-columns: 300px 1fr 300px;
    }
  }

  body {
    background-size: 1000px 1000px;
  }
}

@media (max-width: 1024px) {
  div {
    &.ScrollbarsCustom-Wrapper {
      overflow: auto !important;
      position: relative !important;
    }

    &.ScrollbarsCustom-Scroller {
      position: relative !important;
      overflow: auto !important;
    }
  }
}

::-webkit-scrollbar {
  width: 0;
}

.swiper-button-next, .swiper-button-prev {
  width: 10%;
  height: 100%;
  top: 0;
  position: absolute;
  z-index: 10;
  display: flex;
}

@media (min-width: 1024px) {
  div {
    &.ScrollbarsCustom-Wrapper {
      overflow: hidden !important;
      position: absolute !important;
      inset: 0 !important;
    }

    &.ScrollbarsCustom-Scroller {
      position: absolute !important;
      overflow: hidden scroll !important;
      padding: 0 !important;
      margin: 0 !important;
    }
  }
}