.sec3CatgCardPhotoLeft, .sec3CatgCardPhotoMid, .sec3CatgCardPhotoRight {
  position: relative;
}

.sec1Card {
  cursor: pointer;
}

.linkLineInner, .sec4LinkWrapper .linkLineInner {
  position: absolute;
  top: 0;
  left: 1px;
  bottom: 0;
  border: 0;
  border-bottom: 1px solid #fff;
  width: 0;
  transition: width ease-in-out .2s;
}

.sec3CatgCardPhotoLeft:after, .sec3CatgCardPhotoMid:after, .sec3CatgCardPhotoRight:after {
  cursor: pointer;
}

.linkLineWrapper {
  position: relative;
}

.marginTop .linkLine {
  border-bottom: 1px solid #fff;
  opacity: .4;
}

.sec1LinkWrapper .linkLine, .sec4LinkWrapper .linkLine {
  border-bottom: 1px solid #323942;
  opacity: .3;
}

.sec1LinkWrapper .linkLineInner, .sec4LinkWrapper .linkLineInner {
  border-bottom: 1px solid #323942;
}

.image {
  position: relative;
  overflow: hidden;
  display: inline-flex;
  width: 100%;

  img {
    cursor: pointer;
  }
}

.sec3CatgCardPhotoLeft:after, .sec3CatgCardPhotoMid:after, .sec3CatgCardPhotoRight:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0);
  transition: background-color ease-in 185ms;
}

.sec3CatgCardPhotoLeft:hover:after, .sec3CatgCardPhotoMid:hover:after, .sec3CatgCardPhotoRight:hover:after {
  background-color: rgba(0, 0, 0, 0.1);
}

.sec3PHCat {
  transition: transform cubic-bezier(0.3, 0, 0.75, 1) 0.2s;
  cursor: pointer;

  &:hover {
    transform: scale(1.1);
  }
}

.sec1Card img, .sec4Card img {
  transition: transform cubic-bezier(0.3, 0, 0.75, 1) 0.2s;
}

.sec1Card img:hover, .sec4Card img:hover {
  transform: scale(1.06);
}

.sec4CardLinkWrapper {
  .linkLine {
    width: calc(100% - 1px);
    opacity: .3;
  }

  .linkLineInner {
    width: 0;
  }

  &:hover .linkLineInner {
    width: calc(100% - 1px);
  }
}

.linkWrapper:hover .linkLineInner, .sec4LinkWrapper:hover .linkLineInner {
  width: calc(100% - 1px);
}

.button {
  opacity: .6;
  transition: opacity 150ms ease-in-out;
  position: relative;
  overflow: hidden;

  &:hover {
    opacity: 1;
  }

  &:before {
    animation: backArrow 0.5s cubic-bezier(0, 0.31, 0.88, 0.99);
  }

  &:hover:before {
    animation: forwardArrow 0.5s cubic-bezier(0.68, 0.01, 1, 1);
  }
}

@keyframes forwardArrow {
  0%, 100% {
    transform: translateX(0);
  }

  25% {
    transform: translateX(150%);
  }

  25.1% {
    visibility: hidden;
    transform: translateX(-150%);
  }

  25.2% {
    visibility: visible;
  }

  50.8% {
    transform: translateX(0);
  }
}

@keyframes backArrow {
  0%, 100% {
    transform: translateX(0);
  }

  25% {
    transform: translateX(-150%);
  }

  25.1% {
    visibility: hidden;
    transform: translateX(150%);
  }

  25.2% {
    visibility: visible;
  }

  50.8% {
    transform: translateX(0);
  }
}

