.searchingForm {
  width: 100%;
}

.dropDownCenter {
  transform: translateY(calc(50vh - 113px));
}

.inputWrapper {
  position: relative;
}

.input {
  width: calc(100% - 40px);
  background: 0 0;
  border: none;
  outline: 0;
  height: 30px;
  font-size: 20px;
  line-height: 25px;
  color: #fff;
  margin-top: 30px;
}

.button {
  height: 20px;
  width: 40px;
  cursor: pointer;
  position: absolute;
  top: 50%;
  margin-top: 2px;
  right: 0;
  border: 0;
  background: url("../../assets/images/footer/formMailing/Rectangle 118.svg") no-repeat;
  background-size: cover;
  opacity: .6;
  transition: opacity 150ms ease-in-out;
  overflow: hidden;

  &::before {
    content: "";
    background: url("../../assets/images/footer/formMailing/Arrow 2.svg") no-repeat;
    background-size: cover;
    width: 16px;
    height: 6px;
    position: absolute;
    top: 5px;
    left: 56%;
    margin-left: -10px;
  }
}

.underlineSearching {
  width: 100%;
}

.sizeBoth {
  height: 60px;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 50;
  background-color: transparent;
  transition: background-color .1s ease-in-out;
}

.sizeBoth.active {
  background-color: rgb(237, 236, 233);;
  transition: background-color .1s ease-in-out;
}

.col3 {
  display: grid;
  grid-template-columns: 2fr 1fr 2fr;
  align-items: center;
  justify-items: center;
  width: 100%;
}

.leftSideNav {
  justify-self: start;

  li {
    margin-right: 75px;
    cursor: pointer;
  }
}

.rightSideNav li {
  cursor: pointer;
}

.leftSideNav {
  display: flex;
  align-items: center;
}

.rightSideNav {
  display: flex;
  align-items: center;
  justify-self: end;

  li:nth-child(1) {
    margin-left: 0;
  }
}

.midNav {
  line-height: 0;
  margin-top: 5px;
}

.nav.active {
  color: rgb(50, 57, 66);
}

.nav {
  list-style: none;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: .05em;
  color: #fff;

  * {
    font-family: Montserrat-Light, sans-serif;
  }

}

.busketOuter {
  border: 1px solid #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  position: relative;
}

.busketOuter.active {
  border-color: rgb(50, 57, 66);
}

.busketInner {
  position: absolute;
  top: 50%;
  left: 50%;
  padding-left: 1px;
  transform: translate(-49%, -48%);
}

.seacatacc {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: #596460;
  z-index: 1;
  cursor: default;
  transform: translateY(-100%);
}

.seacatacccrossWrapper {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 51px;

  div {
    cursor: pointer;
    visibility: hidden;
    margin-right: 8px;
    margin-top: 2px;
  }
}

.nav {
  .leftSideNav li > div:nth-child(1), .rightSideNav li > div:nth-child(1) {
    transition: opacity ease-in 150ms;
  }

  .leftSideNav li > div:nth-child(1):hover, .rightSideNav li > div:nth-child(1):hover {
    opacity: .5;
  }
}

.viewOff {
  animation: viewOff .1s ease-in-out;
}

@keyframes viewOn {
  0% {
    background-color: transparent;
  }

  100% {
    background-color: #edece9;
  }
}

@keyframes viewOff {
  0% {
    background-color: #edece9;
  }

  100% {
    background-color: transparent;
  }
}

.button {
  &:hover {
    opacity: 1;
  }

  &:before {
    animation: backArrow 0.5s cubic-bezier(0, 0.31, 0.88, 0.99);
  }

  &:hover:before {
    animation: forwardArrow 0.5s cubic-bezier(0.68, 0.01, 1, 1);
  }
}

@keyframes forwardArrow {
  0%, 100% {
    transform: translateX(0);
  }

  25% {
    transform: translateX(150%);
  }

  25.1% {
    visibility: hidden;
    transform: translateX(-150%);
  }

  25.2% {
    visibility: visible;
  }

  50.8% {
    transform: translateX(0);
  }
}

@keyframes backArrow {
  0%, 100% {
    transform: translateX(0);
  }

  25% {
    transform: translateX(-150%);
  }

  25.1% {
    visibility: hidden;
    transform: translateX(150%);
  }

  25.2% {
    visibility: visible;
  }

  50.8% {
    transform: translateX(0);
  }
}

@media (min-width: 320px) {
  .button {
    right: 0;
    margin-top: 7px;
    height: 16px;
    width: 32px;
  }

  .seacatacccrossWrapper div {
    margin-right: 42px;
    margin-top: 8px;
  }

  .leftSideNav, .tabletAcc, .tabletTrash {
    display: none;
  }

  .mobileAcc, .mobileBurgMenu, .mobileSearch {
    display: block;
    line-height: 0;
    transform: scale(0.9);
  }

  .mobileAcc.active, .mobileBurgMenu.active, .mobileSearch.active {
    filter: invert(72%);
  }

  .rightSideNav {
    margin-top: 0;

    img {
      width: 100%;
      height: auto;
    }

    li {
      margin-left: 12px;
    }
  }

  .col3 {
    grid-template-columns: 1fr 1.3fr;
  }

  .midNav {
    justify-self: flex-start;
    margin-top: 0;

    img {
      width: 100%;
      height: auto;
    }
  }

  .busketWrapper {
    transform: scale(0.9);
  }
}


@media (min-width: 420px) {
  .nav {
    margin-top: 5px;
  }

  .button {
    right: 0;
    margin-top: 7px;
  }

  .seacatacccrossWrapper div {
    margin-top: 18px;
  }

  .rightSideNav {
    margin-top: 5px;
  }

  .busketInner {
    transform: translate(-50%, -49%);
  }
}

@media (min-width: 768px) {
  .button {
    right: 0;
  }

  .seacatacccrossWrapper div {
    margin-right: 60px;
  }

  .rightSideNav li {
    margin-left: 30px;
  }
}

@media (min-width: 1024px) {
  .button {
    margin-top: 6px;
  }

  .seacatacccrossWrapper div {
    margin-right: 9px;
    margin-top: 12px;
  }

  .nav {
    margin-top: 0;

    .rightSideNav {
      margin-top: 0;
    }

    .midNav {
      justify-self: center;
    }
  }

  .busketInner {
    transform: translate(-48%, -49%);
  }

  .leftSideNav, .tabletAcc, .tabletTrash {
    display: flex;
  }

  .mobileAcc, .mobileBurgMenu, .mobileSearch, .mobileSearchLI {
    display: none;
  }

  .col3 {
    grid-template-columns: 2fr 1fr 2fr;
  }

  .rightSideNav li {
    margin-left: 55px;
  }

  .leftSideNav li {
    margin-right: 55px;
  }
}

@media (min-width: 1280px) {
  .button {
    right: 0;
    margin-top: 7px;
    height: 17px;
    width: 34px;

    &:before {
      width: 18px;
      height: 7px;
      top: 5px;
      left: 54%;
    }
  }

  .rightSideNav li {
    margin-left: 73px;
  }

  .leftSideNav li {
    margin-right: 73px;
  }
}

@media (min-width: 1600px) {
  .rightSideNav li {
    margin-left: 75px;
  }

  .leftSideNav li {
    margin-right: 75px;
  }
}

@media (min-width: 2560px) {
  .busketInner {
    transform: translate(-48%, -46%);
  }
}