.col3 {
    display: grid;
    grid-template-columns: 1fr 4fr 1fr;
    align-items: center;
    justify-items: center;
    width: 100%;
}

.toTop {
    display: flex;

    .button {
        position: relative;
    }
}

.col2L1R1R {
    display: grid;
    grid-template-columns: 1.5fr 1fr 1fr;
    justify-items: center;
    width: 100%;
}

.top {
    background: #323942;
}

.under {
    height: 60px;
    background: #23272d;
}

.lic {
    font-size: 18px;
    line-height: 22px;
    letter-spacing: .05em;
    color: #fff;
    opacity: .6;
    align-self: center;
    font-family: Montserrat-Light,sans-serif;
}

.toTop {
    position: relative;
    justify-self: start;
    cursor: pointer;
    margin-left: 72px;
}

.headMailing {
    font-size: 48px;
    line-height: 28px;
    letter-spacing: .05em;
    color: #fff;
}

.table {
    color: #fff;
    text-decoration: none;
    border-collapse: collapse;
    text-align: left;

    a {
        color: #fff;
        text-decoration: none;
    }

    th {
        border-left: 1px solid #fff;
        padding-left: 18px;
        font-size: 20px;
        line-height: 16px;
        font-weight: 300;
        padding-top: 17px;
    }

    td {
        border-left: 1px solid #fff;
        padding-left: 18px;
        font-size: 20px;
        line-height: 16px;
        font-weight: 300;
        padding-top: 17px;

        div {
            cursor: pointer;
        }
    }

    th {
        font-size: 28px;
        line-height: 18px;
        letter-spacing: .05em;
        padding-bottom: 30px;
        font-weight: 300;
        padding-top: 0;
    }
}

.noSelect {
    user-select: none;
}

.mailingForm {
    width: 450px;
}

.textMailing {
    font-size: 18px;
    line-height: 23px;
    color: #fff;
    margin-top: 20px;
}

.input {
    width: 390px;
    background: 0 0;
    border: none;
    outline: 0;
    height: 30px;
    font-size: 20px;
    line-height: 25px;
    color: #fff;
    margin-top: 30px;
}

.button {
    height: 20px;
    width: 40px;
    cursor: pointer;
    position: absolute;
    top: 50%;
    margin-top: 2px;
    right: 0;
    border: 0;
    background: url("../../assets/images/footer/formMailing/Rectangle 118.svg") no-repeat;
    background-size: cover;

    &::before {
        content: "";
        background: url("../../assets/images/footer/formMailing/Arrow 2.svg") no-repeat;
        background-size: cover;
        width: 16px;
        height: 6px;
        position: absolute;
        top: 5px;
        left: 56%;
        margin-left: -10px;
    }
}

.social {
    list-style: none;
    display: flex;
    align-items: center;
    margin-top: 60px;
}

.mailingWrapper {
    justify-self: start;
}

.shopWrapper, .supportWrapper {
    justify-self: end;
}

.underlineMailing {
    margin-top: 2px;
}

.inputWrapper {
    position: relative;
}

.social li {
    margin-right: 40px;
    cursor: pointer;
    transition: opacity ease-in 150ms;
}

.shopWrapper td div, .supportWrapper td div {
    position: relative;
    width: fit-content;
}

.shopWrapper td div:after, .supportWrapper td div:after {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0;
    border-bottom: 1px solid #fff;
    transition: width ease-in-out .2s;
}

.social li:hover {
    opacity: .6;
}

.shopWrapper td div:hover:after, .supportWrapper td div:hover:after {
    width: 100%;
}

.button {
    opacity: .6;
    transition: opacity 150ms ease-in-out;
    overflow: hidden;

    &:hover {
        opacity: 1;
    }

    &:before {
        animation: backArrow 0.5s cubic-bezier(0, 0.31, 0.88, 0.99);
    }

    &:hover:before {
        animation: forwardArrow 0.5s cubic-bezier(0.68, 0.01, 1, 1);
    }
}

@keyframes forwardArrow {
    0%,100% {
        transform: translateX(0);
    }

    25% {
        transform: translateX(150%);
    }

    25.1% {
        visibility: hidden;
        transform: translateX(-150%);
    }

    25.2% {
        visibility: visible;
    }

    50.8% {
        transform: translateX(0);
    }
}

@keyframes backArrow {
    0%,100% {
        transform: translateX(0);
    }

    25% {
        transform: translateX(-150%);
    }

    25.1% {
        visibility: hidden;
        transform: translateX(150%);
    }

    25.2% {
        visibility: visible;
    }

    50.8% {
        transform: translateX(0);
    }
}

@media (min-width: 320px) {
    .col2L1R1R {
        grid-template-columns: 1fr 1fr;
        grid-template-areas: "top top" "left right";
    }

    .mailingForm {
        width: 100%;
    }

    .shopWrapper, .supportWrapper {
        justify-self: flex-start;
    }

    .mailingWrapper {
        grid-area: top;
        width: 100%;
    }

    .shopWrapper {
        grid-area: left;
    }

    .supportWrapper {
        grid-area: right;
        justify-self: flex-end;
    }

    .headMailing {
        font-size: 24px;
        line-height: 30px;
    }

    .textMailing {
        margin-top: 5px;
        font-size: 12px;
        line-height: 15px;
    }

    .social li {
        transform: scale(0.8);
    }

    .input {
        font-size: 14px;
        line-height: 18px;
        width: 87%;
        margin-top: 6px;
    }

    .button {
        right: 0;
        margin-top: -7px;
        height: 16px;
        width: 32px;
    }

    .underlineMailing {
        width: 100%;
    }

    .social {
        margin-top: 26px;
        margin-bottom: 26px;
        justify-content: center;

        li:last-child {
            margin-right: 0;
        }
    }

    .table {
        td {
            font-size: 12px;
            line-height: 17px;
            padding-top: 6px;
            padding-left: 6px;
        }

        th {
            line-height: 17px;
            padding-left: 6px;
            font-size: 20px;
            padding-bottom: 10px;
            padding-top: 0;
        }
    }

    .top {
        padding-top: 12px;
        padding-bottom: 30px;
    }

    .lic {
        font-size: 8px;
        line-height: 10px;
    }

    .toTop {
        margin-left: 0;
        transform: scale(0.8) rotateZ(-90deg);
    }

    .under {
        height: 50px;
    }

    .col3 {
        grid-template-columns: 1fr 8fr 1fr;
    }
}

