.innerContent a {
  text-decoration: none;
}

.container {
  display: grid;
  grid-template-areas: "leftZero content rightZero";
  grid-template-columns: 35px 1fr 35px;
  align-items: center;
  justify-items: center;
}

.cat {
  align-self: center;
}

.content {
  grid-area: content;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
}

.cat img {
  max-width: 100%;
  max-height: 100%;
}

.innerContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.buttonWrapper {
  display: grid;
  grid-template-areas: "text button" "line line";

  div {
    grid-area: text;
  }

  hr {
    opacity: .6;
    grid-area: line;
    border: none;
    border-bottom: 1px solid #000;
  }
}

.button {
  height: 20px;
  margin-left: 20px;
  width: 40px;
  cursor: pointer;
  position: relative;
  border: 0;
  background: url("../../../assets/images/sections/popularCatg/Rectangle 118.svg") no-repeat;
  background-size: cover;
  grid-area: button;

  &::before {
    content: "";
    background: url("../../../assets/images/sections/popularCatg/Arrow 2.svg");
    background-size: cover;
    width: 20px;
    height: 0;
    position: absolute;
    top: 1px;
    left: 50%;
    margin-left: -10px;
  }
}

.sec {
  position: fixed;
  top: 50%;
  right: 0;
  left: 0;
  transform: translateY(-50%);
}

.button {
  opacity: .6;
  transition: opacity 150ms ease-in-out;
  overflow: hidden;
}

.buttonWrapper:hover .button {
  opacity: 1;
}

.button:before {
  animation: backArrow 0.5s cubic-bezier(0, 0.31, 0.88, 0.99);
}

.buttonWrapper:hover .button:before {
  animation: forwardArrow 0.5s cubic-bezier(0.68, 0.01, 1, 1);
}

@keyframes forwardArrow {
  0%, 100% {
    transform: translateX(0);
  }

  25% {
    transform: translateX(150%);
  }

  25.1% {
    visibility: hidden;
    transform: translateX(-150%);
  }

  25.2% {
    visibility: visible;
  }

  50.8% {
    transform: translateX(0);
  }
}

@keyframes backArrow {
  0%, 100% {
    transform: translateX(0);
  }

  25% {
    transform: translateX(-150%);
  }

  25.1% {
    visibility: hidden;
    transform: translateX(150%);
  }

  25.2% {
    visibility: visible;
  }

  50.8% {
    transform: translateX(0);
  }
}

@media (min-width: 320px) {
  .container {
    grid-template-columns: 36px 1fr 36px;
  }

  .content {
    grid-template-columns: 1fr;
    text-align-last: center;
  }

  .cat img {
    width: 30vmin;
  }

  .button {
    height: 14px;
    margin-left: 6px;
    align-self: center;
    width: 28px;
  }

  .buttonWrapper div {
    font-size: 16px;
    line-height: 20px;
    color: #596460;
  }

  .errorTextDig {
    font-size: 100px;
    line-height: 74px;
    letter-spacing: .05em;
    font-family: Montserrat-Medium, sans-serif;
    color: #596460;
    margin-top: 8px;
    margin-bottom: 14px;
  }

  .errorText {
    font-size: 15px;
    line-height: 20px;
    color: #596460;
  }

  .button::before {
    width: 16px;
    height: 6px;
    top: 3px;
    margin-top: 1px;
    margin-left: -7px;
  }
}

@media (min-width: 420px) {
  .container {
    grid-template-columns: 66px 1fr 66px;
  }

  .button {
    margin-top: 2px;
  }

  .errorTextDig {
    font-size: 120px;
    line-height: 88px;
    margin-bottom: 18px;
    margin-top: 14px;
  }

  .buttonWrapper div, .errorText {
    font-size: 18px;
    line-height: 23px;
  }
}

@media (min-width: 768px) {
  .container {
    grid-template-columns: 74px 1fr 74px;
  }

  .content {
    grid-template-columns: 1fr 1fr;
  }

  .cat img {
    width: 60vmin;
  }

  .errorTextDig {
    font-size: 143px;
    line-height: 103px;
    margin-bottom: 16px;
  }

  .buttonWrapper div, .errorText {
    font-size: 18px;
    line-height: 23px;
  }
}

@media (min-width: 1024px) {
  .container {
    grid-template-columns: 94px 1fr 94px;
  }

  .button {
    margin-top: 2px;
    height: 16px;
    width: 32px;

    &:before {
      margin-top: 2px;
    }
  }

  .errorTextDig {
    font-size: 193px;
    line-height: 140px;
    margin-bottom: 22px;
  }

  .buttonWrapper div, .errorText {
    font-size: 24px;
    line-height: 30px;
  }
}

@media (min-width: 1280px) {
  .container {
    grid-template-columns: 160px 1fr 160px;
  }

  .errorTextDig {
    font-size: 222px;
    line-height: 160px;
  }

  .buttonWrapper div, .errorText {
    font-size: 28px;
    line-height: 35px;
  }
}

@media (min-width: 1600px) {
  .container {
    grid-template-columns: 178px 1fr 178px;
  }

  .button {
    margin-top: 2px;
  }

  .errorTextDig {
    font-size: 288px;
    line-height: 208px;
    margin-bottom: 30px;
  }

  .buttonWrapper div, .errorText {
    font-size: 36px;
    line-height: 45px;
  }

  .button {
    height: 20px;
    width: 40px;
    margin-top: 3px;

    &:before {
      width: 23px;
      height: 9px;
      top: 4px;
      margin-top: 2px;
      margin-left: -11px;
    }
  }
}

@media (min-width: 1920px) {
  .container {
    grid-template-columns: 302px 1fr 302px;
  }
}

@media (min-width: 2560px) {
  .container {
    grid-template-columns: 658px 1fr 658px;
  }
}