.preloader {
  position: absolute;
  height: 100%;
  width: 100%;
  opacity: 0;
  display: flex;
  align-content: center;
  justify-content: center;
  align-items: center;
  transition: all 150ms ease-in-out;
  z-index: -1;
}

.preloader.active {
  background-color: #596460;
  opacity: 1;
  transition: all 150ms ease-in-out;
  z-index: 998;
}

.preloader svg {
  width: auto;
  height: 100%;
}