.terms {
    margin-top: 250px;
    margin-bottom: 250px;
}

.list li, .title {
    color: #323942;
    font-weight: 300;
}

.list {
    list-style: none;
    counter-reset: li;

    ol {
        list-style: none;
        counter-reset: li;
    }

    li:before {
        counter-increment: li;
        content: counters(li, ".") ". ";
    }

    ul {
        list-style: none;

        li:before {
            content: "- ";
            counter-increment: none;
        }
    }
}

.questFo ul > li:before {
    content: "";
    counter-increment: none;
}

@media (min-width: 320px) {
    .copyright ol > li {
        margin-top: 4px;
    }

    .responsibility ol > li {
        margin-top: 4px;

        &:nth-child(3) > ol > li {
            margin-top: 4px;
        }
    }

    .questT ol > li:nth-child(1) {
        margin-top: 4px;
    }

    .questFo {
        ol > li, ul > li {
            margin-top: 4px;
        }
    }

    .list li ol > li:before {
        content: "";
    }

    .questT ol {
        > li:last-child {
            margin-top: 4px;
        }

        li ol li {
            margin-top: 0 !important;
        }
    }

    .responsibility ol > li * {
        margin-top: 0 !important;
    }

    .title {
        margin-bottom: 21px;
        font-size: 18px;
        line-height: 23px;
        letter-spacing: .05em;
    }

    .list {
        li {
            font-size: 10px;
            line-height: 13px;
            letter-spacing: .05em;
        }

        > li {
            font-size: 13px;
            line-height: 16px;
            letter-spacing: .05em;
            margin-bottom: 13px;
        }
    }

    .terms {
        margin-top: 118px;
        margin-bottom: 82px;
    }
}

